import React, { Component } from "react";
import {
  Form,
  Grid,
  Header,
  Segment,
  TransitionablePortal,
  Button
} from "semantic-ui-react";

const transitions = [
  "browse",
  "browse right",
  "drop",
  "fade",
  "fade up",
  "fade down",
  "fade left",
  "fade right",
  "fly up",
  "fly down",
  "fly left",
  "fly right",
  "horizontal flip",
  "vertical flip",
  "scale",
  "slide up",
  "slide down",
  "slide left",
  "slide right",
  "swing up",
  "swing down",
  "swing left",
  "swing right",
  "zoom"
];
const options = transitions.map(name => ({
  key: name,
  text: name,
  value: name
}));

export default class TransitionablePortalExampleTransition extends Component {
  state = { animation: "fade up", duration: 1200, open: false };

  handleClick = () => this.setState({ open: !this.state.open });

  renderPortalContent = () => {
    return (
      <Segment
        style={{
          left: "40%",
          position: "fixed",
          top: "50%",
          zIndex: 1000
        }}
      >
        <Header>This is a controlled portal</Header>
        <p>Portals have tons of great callback functions to hook into.</p>
        <p>To close, simply click the close button or click away</p>
      </Segment>
    );
  };

  render() {
    const { animation, duration, open } = this.state;

    return (
      <Grid columns={2}>
        <Grid.Column>
          <Form>
            <Form.Button
              content={open ? "Close Portal" : "Open Portal"}
              negative={open}
              positive={!open}
              onClick={this.handleClick}
            />
          </Form>
          <Button.Group size="large">
            <Button>One</Button>
            <Button>Two</Button>
            <Button>Three</Button>
          </Button.Group>
          <TransitionablePortal
            open={open}
            transition={{ animation, duration }}
          >
            {this.renderPortalContent()}
          </TransitionablePortal>
        </Grid.Column>
      </Grid>
    );
  }
}
